.cards-wrapper {
  perspective: 150vw;
  position: relative;
  top: -270px;
}

.cards-container {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 1.3vw;
  transform: rotateY(30deg) rotateX(29deg); /* Додає обертання контейнера по обох осях */
  transform-style: preserve-3d; /* Забезпечує 3D ефект для дочірніх елементів */
  width: 240vw; /* Додає ширину, щоб контейнер містив усі елементи */
  height: 240vh; /* Додає висоту, щоб контейнер містив усі елементи */
}

/* Ряд карток */
.cards-row {
  display: flex;
  gap: 1vw;
  transform-style: preserve-3d; /* Забезпечує 3D ефект для дочірніх елементів */
  position: relative;
}

.cards-row-0{
  left: 25vw;
  animation: anim_row 22s linear infinite;
}
.cards-row-1{
  left: 10vw;
  animation: anim_row 16s linear infinite;
}
.cards-row-2{
  left: 8vw;
  animation: anim_row 18s linear infinite;
}
.cards-row-3{
  left: 7vw;
  animation: anim_row 20s linear infinite;
}

/* Стилі для карток */
.card {
  filter: blur(4px)
  
}

/* Зображення всередині карток */
.card img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

/* Анімація руху вверх */
@keyframes anim_row {
  0% {
  }
  100% {
    transform: translateX(-120.5vw);
  }
}
